ight: 150% !important;\n    width: 100% !important;\n    transition: all 0.3s ease;\n    z-index: 0;\n  }\n\n  .card-img-caption .my-carousel-caption2 {\n    opacity: 1;\n    z-index: 1;\n    /* transition: all 0.4s; */\n    background-image: linear-gradient(\n      transparent,\n      rgba(0, 0, 0, 0.856) 80%,\n      black 100%\n    );\n    bottom: 50px;\n    padding-bottom: 30px;\n    position: relative;\n    width: 99%;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */";